import request from '@/apis/request.wms'

// 
export function getListApi(data = {}) {
  return request({
    url: '/warehouseMgt/wmsDeliveryMaterialDetail/pageList',
    method: 'post',
    data,
    timeout: 2 * 60 * 1000
  })
}

